<template>
  <div class="map" id="container">
    
  </div>
</template>

<script>
import bikeLogo from '../assets/bike.png'
import posLogo from '../assets/location.png'
export default {
  name: 'hello',
  data () {
    return {
      
    }
  },
  methods: {
    addMarker: function(map, point, data){  // 创建图标对象   
      var myIcon = new BMap.Icon(bikeLogo, new BMap.Size(46, 46), {  
         anchor: new BMap.Size(10, 25),    
      });
      // 如果是位置信息的话，则图标替换成位置图片。
      if(data == null) {
        myIcon = new BMap.Icon(posLogo, new BMap.Size(46, 46), {  
           anchor: new BMap.Size(10, 25),    
        });
      }
       var self = this;
      // 创建标注对象并添加到地图   
       var marker = new BMap.Marker(point, {icon: myIcon});    
       map.addOverlay(marker); 
       if(data) {
         marker.addEventListener("click", function(e){
          console.log(e.target);
          if(e.target.Yc) {
            e.target.Yc.className += " fadeOut";
          }
          // 延时跳转，为了能够做动画。
          setTimeout(()=>{
            self.$router.push({name: "Detail", params: data});
            if(e.target.Yc) {
              e.target.Yc.className = e.target.Yc.className.replace("fadeOut", "");
            }
          }, 650);
         })  
       }
    }  
  },
  mounted() {
    var map = new BMap.Map("container");          // 创建地图实例  
    var point = new BMap.Point(116.404, 39.915);  // 创建点坐标  

    // 定位
    var self = this;
    var geolocation = new BMap.Geolocation();
    geolocation.getCurrentPosition(function(r){
      // 定位完成后给body背景色设置成默认色
      document.body.className = "default";

      if(this.getStatus() == BMAP_STATUS_SUCCESS){
        // alert('您的位置：'+r.point.lng+','+r.point.lat);
        point = new BMap.Point(r.point.lng, r.point.lat);
      }
      else {
        alert('failed'+this.getStatus());
      }  
      // 初始化地图，设置中心点坐标和地图级别        
      map.centerAndZoom(point, 15);   

      self.addMarker(map, point, null);  

      // 取得数据
      console.log(r.point);
      fetch(`/api/broken-bikes?lat=${r.point.lat}&lng=${r.point.lng}`)
      .then((res)=>{
        console.log(res);
        return res.json();
      }).then((json)=>{
        for(let i=0;i<json.length;i++) {
          let point = new BMap.Point(json[i].lng, json[i].lat);
          self.addMarker(map, point, json[i]);
        }
      }) 

      
    },{enableHighAccuracy: true})
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
#container {
  height: 100%;
  background-color: transparent !important;
}
body {
  background: url(../assets/home_bg2.jpg) no-repeat left center;
  animation: move 15s linear;
}
body.default {
  background: #f5f3f0;
}
@keyframes move {
  0% {
    background-position: 20% center;
    background-size: auto 110%;
  }
  100% {
    background-position: 100% center;
    background-size: auto 100%;
  }
}
.fadeOut {
  animation: fadeOut 1s ease-out;
}
@keyframes fadeOut {
  0% {
    transform: scale(1.0);
    opacity: 1;
  }
  100% {
    transform: scale(3.5);
    opacity: 0;
  }
}
.BMap_Marker div {
  border-radius: 50%;
}
.BMap_Marker img {
  height: 100%;
}
</style>



