<template>
  <div class="detail">
    <h2>车辆编号：{{$route.params.id}}</h2>
    <h2>故障信息：{{$route.params.detail}}</h2>
    <mt-checklist
      title=""
      v-model="method"
      :options="['更换二维码', '更换轮胎', '更换其他配件']">
    </mt-checklist>
    <mt-button type="primary" @click="repairDone" size="large">维修完成</mt-button>
    <mt-button type="default" @click="back" size="large">返回</mt-button>
  </div>
</template>

<script>
import { Toast, Indicator } from 'mint-ui';
export default {
  name: 'hello',
  data () {
    return {
    	method: []
    }
  },
  activated() {
    this.method = [];
  },
  methods: {
  	repairDone() {
      const duration = 2000;
      // 输入检查
      if(this.method == null || this.method.length == 0) {
          Toast({
            message: "请选择维修方法。",
            position: 'middle',
            duration: duration
          });
          return;
      }


      // 启动loading
      Indicator.open({
        text: 'Loading...',
        spinnerType: 'fading-circle'
      });

  		let id = this.$route.params.id;
  		let method = this.method;
  		fetch(`/api/repair?id=${id}&method=${method}`)
  		.then((res)=>{
        Indicator.close(); // 关闭loading
  			return res.json();
  		}).then((json)=>{
  			if(json.message) {
          Toast({
            message: json.message,
            position: 'middle',
            duration: duration,
            iconClass: "mint-toast-icon mintui mintui-success"
          });
          setTimeout(()=>{
            this.back();
          }, duration);
  			} else {
          Toast({
            message: json.err_message,
            position: 'middle',
            duration: duration
          });
        }
  		})
  	},
  	back() {
  		this.$router.push({path: "/"});
  	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.mint-button--large {
  width: 90%;
  margin: 10px auto 0 auto;
}
.mint-checklist {
  text-align: left;
}
.detail h2 {
  text-align: left;
  padding-left: 5%;
}
.detail {
  background: url(../assets/home_bg2.jpg) no-repeat -200px center;
}
</style>



